.gpt3__blog-container_article {
  background: var(--color-blog);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
      overflow: hidden;
    border-top-left-radius: 45px;
}
.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-blog);
}
.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}
.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
  padding: var(--p-24) var(--p-30);
  color: var(--color-light);
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-weight: 700;
  font-size: 12px;
  line-height: 35px;
}
.gpt3__blog-container_article-content h3 {
  font-weight: 800;
  font-size: 25.11px;
  line-height: 30px;
  display: flex;
  flex: 1;
}
.gpt3__blog-container_article-content a {
  font-weight: 700;
  font-size: 12px;
  line-height: 35px;
}

@media screen and(max-width: 768px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25px;
  }
}
