.gpt3__footer {
  background-color: var(--color-footer);
  display: flex;
  flex-direction: column;
  gap: 80px;
  padding: var(--p-45);
  padding-bottom: var(--p-20) !important;
}
.gpt3__footer-heading {
  text-align: center;
  font-weight: var(--bold);
  font-size: 50px;
  line-height: 75px;
  letter-spacing: -0.04em;
}
.gpt3__footer-link {
  border: 1px solid var(--color-light);
  width: 218px;
  height: 65px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  font-size: 18px;
  line-height: 21px;
}
.gpt3__footer-contact {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 75px;

}
.gpt3__footer-image img {
  margin-bottom: var(--m-16);
}
.gpt3__footer-image p {
  color: var(--color-light);
  font-size: 12px;
  line-height: 14px;  
}

.gpt3__footer-ul h3 {
  color: var(--color-light);
  font-size: 20px;
  line-height: 16px;
  margin-bottom: var(--m-16);
}
.gpt3__footer-ul li {
  margin-bottom: var(--m-16);
}
.gpt3__footer-ul li a {
  color: var(--color-light);
  font-size: 14px;
}
.gpt3__footer-copyrights {
  color: var(--color-light);
  font-size: 12px;
  line-height: 14px;
  text-align: center;
}
@media screen and (max-width: 992px) {
.gpt3__footer-contact {
  justify-items: center;
}
}
@media screen and (max-width: 768px) {
  .gpt3__footer-contact {
  justify-items: flex-start;
}
  .gpt3__footer-heading {
    font-size: 30px;
      line-height: 50px;
  }


}