.gpt3__header {
  display: flex;
  align-items: center;
  justify-content: space-between;

}
.gpt3__header-left-section {
  flex-basis: 48%;
}
.gpt__header-title {
  font-size: 45px;
  font-weight: var(--bold);
  margin-bottom: var(--m-24);
}
.gpt3__header-text {
  color: var(--color-text);
  line-height: 1.4;
  margin-bottom: var(--m-24);
}
.gpt3__header-form {
  
}
.gpt3__header-form_email,
.gpt3__header-form_btn-submit {
  border: none;
  outline: none;
  margin-bottom: var(--m-24);
}
.gpt3__header-form_email {
  padding: var(--p-15) var(--p-30);
  background: #052D56;
  border-top-left-radius: var(--rounded);
  border-bottom-left-radius: var(--rounded);
  caret-color: var(--color-text);
  width: calc(100% - 200px);
  color: var(--color-light);
}
::placeholder {
  color: var(--color-text);
}
.gpt3__header-form_btn-submit {
  color: var(--color-light);
  padding: var(--p-15);
  background-color: var( --btn-color);
  border-top-right-radius: var(--rounded);
  border-bottom-right-radius: var(--rounded);
  cursor: pointer;
}
.gpt3__header-people {
  display: flex;
  gap: 12px;
  align-items: center;
}
.gpt3__header-people_text {
  color: var(--color-light);
  font-size: 13px;
}

.gpt3__header-img {
  /* width: 600px; */
  text-align: right;
}

.gpt3__header-img img {
  width: 600px;
}

@media screen and (max-width: 1200px) {
  .gpt__header-title {
    font-size: 30px;
  }
.gpt3__header-text {
  font-size: 15px;
}
  .gpt3__header-img img {
    width: 500px;
  }
  .gpt3__header-people_img img {
    width: 150px;
  }
  .gpt3__header-people_text {
    font-size: 12px;
  }
  /* .gpt3__header-img img {
  height: 460px;
} */
}

@media screen and (max-width: 992px) {
  .gpt3__header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .gpt3__header-img {
    text-align: center;
  }
  .gpt3__header-people {
    flex-direction: column;
  }
}

@media screen and (max-width: 576px) {
  .gpt__header-title {
    font-size: 22px;
  }
  .gpt3__header-text {
    font-size: 13px;
    max-width: 400px;
    margin: 0  auto var(--m-24);
  }
  .gpt3__header-form {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  .gpt3__header-form_email {
    width: 100%;
    margin: 0;
    border-radius: var(--rounded);
  }
  .gpt3__header-form_btn-submit {
    width: 200px;
    margin: 0  auto var(--m-24);
    border-radius: var(--rounded);
  }
.gpt3__header-img img {
    width: 350px;
  }
}