.gpt3__navbar {
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  align-items: center;
}
.gpt3__navbar-links_logo {
  width: 62.56px;
  height: 16.02px;
}
.gpt3__navbar-links_container {
  display: flex;
  color: var(--color-light);
  gap: 30px;
}
.gpt3__navbar-links_a,
.gpt3__navbar-links_sign-in a,
.gpt3__navbar-links_sign-up a {
  font-size: var(--normal-font-size);
  font-weight: var(--semi-bold);
  cursor: pointer;
}
.gpt3__navbar-links_sign {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  gap: 20px;
}
.gpt3__navbar-links_sign-in,
.gpt3__navbar-links_sign-up {
  outline: none;
  border: none;
  background-color: transparent;
  color: var(--color-light);
}
.gpt3__navbar-links_sign-up a {
  background-color: var(--btn-color);
  padding: 12px var(--p-20);
  border-radius: 5px;
}

.gpt3__navbar-menu {
  display: none;
  position: relative;
}
.gpt3__navbar-menu svg {
  cursor: pointer;
  transition: 0.9s;
}
.gpt3__navbar-menu_container-links {
  flex-direction: column;
  display: flex;
  color: white;
  position: absolute;
  right: 0;
  background: var(--color-footer);
  min-width: 210px;
  gap: 25px;
  padding:var(--p-25) var(--p-15);
  top: 32px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  display: none;
}

@media screen and (max-width: 992px) {
  .gpt3__navbar-links_container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: block;
    margin-left: 12px;
  }
  .gpt3__navbar-menu_container-links {
    display: flex;
  }
  .gpt3__navbar-menu_sign {
    display: none;
  }
}
@media (max-width: 576px) {
  .gpt3__navbar-menu_sign {
    display: flex;
    gap: 12px;
  }
  .gpt3__navbar-menu_container-links {
    top: 15px;
  }
  .gpt3__navbar-links_sign-up a {
    padding: var(--p-10) var(--p-20);
  }
  .gpt3__navbar-links_sign {
    display: none;
  }
}
