.gpt3__features-title {
  color: var(--color-light);
  display: flex;
  align-self: flex-start;
  flex-direction: column;
      margin-bottom: var(--m-8);
}
.gpt3__features-title h1 {
  font-size: 22px;
}
.gpt3__features-title span {
  width:38px;
  height: 3px;
  background: var(--gradient-bar);
  display: block;
  margin-bottom: 4px;
}

.gpt3__features-text {
  max-width: 650px;
  color: var(--color-text);
}
.gpt3__features-text p {
  font-size: 14px;
}