@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

:root {
  --font-family: 'Manrope', sans-serif;
  --rounded: 5px;
  /* ====== Colors ======*/
  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);
  --color-bg: #040c18;
  --color-footer: #031b34;
  --color-blog: #042c54;
  --color-text: #81afdd;
  --color-subtext: #ff8a71;
  --color-light: #fff;
  --btn-color: #FF4820;
  /* ====== Sections Padding ======*/
  --padding-top: 4rem;
  --padding-bottom: 4rem;

  /* ====== Elements Padding ======*/
  --p-5: 0.3125rem;
  --p-10: 0.625rem;
  --p-15: 0.9375rem;
  --p-20: 1.25rem;
  --p-25: 1.5625rem;
  --p-30: 1.875rem;
  --p-35: 2.1875rem;
  --p-40: 2.5rem;
  --p-45: 2.8125rem;

  --p-4: 0.25rem;
  --p-8: 0.5rem;
  --p-12: 0.75rem;
  --p-16: 1rem;
  --p-20: 1.25rem;
  --p-24: 1.5rem;
  --p-28: 1.75rem;
  --p-30: 2rem;

  /* ========== Margins ========== */
  --m-8: 0.5rem;
  --m-12: 0.75rem;
  --m-16: 1rem;
  --m-24: 1.5rem;
  --m-32: 2rem;

  /* ====== Font Weight ======*/
  --semi-bold: 500;
  --bold: 800;
}
::-webkit-scrollbar {
  width: 16px;
}

::-webkit-scrollbar-track {
  background: var(--color-bg);
}

::-webkit-scrollbar-thumb{
  background: var(--color-blog);
  box-shadow: 0px 0px 30px 30px var(--color-blog);
}

.section__padding {
  padding-top: var(--padding-top);
  padding-bottom: var(--padding-bottom);
}
.container {
  padding-right: 0.75rem;
  padding-left: 0.75rem;
  margin-right: auto;
  margin-left: auto;
}

@media screen and (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media screen and (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
