.gpt3__cta {
  background: var(--gradient-bar);
  padding: var(--p-30);
  border-radius: var(--rounded);
  display: flex;
  align-items: center;
  justify-content: center;
}
.gpt3__cta-text {
  flex-basis: 80%;
}
.gpt3__cta-text p {
  font-weight: var(--semi-bold);
}
.gpt3__cta-text h1 {
  font-weight: var(--bold);
font-size: 24px;
line-height: 45px;
}
.gpt3__cta-btn {
  display: flex;
  justify-content: flex-end;
  flex-basis: 20%;
}
.gpt3__cta-btn a {
  padding: var(--p-20) var(--p-30);
  display: block;
  background-color: black;
  color: var(--color-light);
  text-align: center;
  border-radius: 40px;
}
@media screen and (max-width: 992px) {
  .gpt3__cta {
    flex-direction: column;
    gap: 30px;
  }
    .gpt3__cta-text p {
      text-align: center;
    }
  .gpt3__cta-text h1 {
    margin-top: var(--m-8);
    width: 100%;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
  }
  .gpt3__cta-btn a {
    padding: var(--p-15) var(--p-25);
  }
}