.gpt3__features-container {
  display: flex;
  justify-content: space-between;
}

.gpt3__features-left-section {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.gpt3__features-left-section h1 {
max-width: 400px;
}

.gpt3__features-left-section p {
  color: var(--color-subtext);
  font-size: 14px;

}
.gpt3__whatgpt3-feature {
  margin-bottom: 50px;
  gap: 1px;
}
.gpt3__features-title {
  max-width: 200px;
}
.gpt3__features-title h1 {
  font-size: 18px;
}
.gpt3__features-text {
  max-width: 400px;
}

@media screen and (max-width:992px) {
  .gpt3__features-container {
    flex-direction: column;
    gap: 50px;
  }
  .gpt3__whatgpt3-feature {
    align-items: flex-start !important;
  }
.gpt3__features-left-section h1 {
    max-width: 100%;
    font-size: 24px;
    text-align: center;
  }
  .gpt3__features-left-section p {
    text-align: center;
  }
  .gpt3__whatgpt3-feature {
    flex-direction: row !important;
}
}

@media screen and (max-width:768px) {
.gpt3__whatgpt3-feature {
  flex-direction: column !important;
}
}