.gpt3__possibilities {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
}
.gpt3__possibilities-image {
  flex-basis: 50%;
  display: flex;

}
.gpt3__possibilities-image img {
      width: 500px;
}
.gpt3__possibilities-text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-basis: 50%;
  gap: 30px;
}
.gpt3__possibilities-text p:first-child {
  color: #71E5FF;
  font-weight: var(--semi-bold);
}
.gpt3__possibilities-text p:not(:first-child){
  color: var(--color-text);
  line-height: 28px;
}
.gpt3__possibilities-text h2 {
  font-weight: var(--bold);
}

.gpt3__possibilities-text span {
  color: var(--color-subtext);
  cursor: pointer;
  font-weight: var(--semi-bold);
}

@media screen and (max-width: 768px) {
  .gpt3__possibilities {
    flex-direction: column;
  }
}