.gpt3__blogs {
  display: flex;
  flex-direction: column;
}
.gpt3__blog-heading {
  margin-bottom: var(--m-32);
}
.gpt3__blog-heading h1 {
  font-weight: var(--bold);
font-size: 50px;
line-height: 75px;
letter-spacing: -0.04em;
}

.gpt3__blog-container {
    display: flex;
    gap: 30px;
}
.gpt3__blog-container_left-section {
  flex-basis: 40%;
}
.gpt3__blog-container_right-section {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr ;
  gap: 30px;
}

@media screen and (max-width: 992px) {
  .gpt3__blog-heading h1 {
    font-size: 30px;
    line-height: 50px;
  }
  .gpt3__blogs .gpt3__blog-container {
    display: flex;
    flex-direction: column-reverse;
  }
}

@media (max-width: 992px) {
  .gpt3__blog-container_left-section {
    width: 48%;
    margin: auto;
  }
}
@media screen and (max-width: 768px) {
    .gpt3__blog-container_left-section {
      width: 100%
    }
    .gpt3__blog-heading h1 {
    font-size: 25px;
    line-height: 35px;
  }
  .gpt3__blog-container_right-section {
    display: grid;
    grid-template-columns: 1fr
  }
}